
.redact-prod {
    padding: 10px 20px;
    width: 72%;
}

.no-correct {
    border-bottom: 2px solid #DC3545FF !important;
}

.after-star::after {
    content: " *";
    color: #FF8103;
}

.categories-container {
    width: 100%;
}

.categories-item {
    width: 300px;
    border: none;
    outline: none;
    background-color: #fff;
    padding: 14px;
    margin: 2px 10px;
    border-radius: 6px;
    color: #A4A6AA;
}

.categories-item option {
    color: #444;
}

/* Info-container */

.info-prod-container {
    width: 100%;
    margin-top: 25px;
    border-top: 2px solid #E4E4E4;
    padding: 10px;
}

.redact-container {
    display: flex;
    justify-content: space-between;
}

.redact-item {
    width: 100%;
    padding: 5px;
}

.redact-item input[type='file']::after {
    content: "+";
    float: right;
    color: #FF8103;
    font-size: 20px;
    line-height: 15px;
}

::-webkit-file-upload-button {
    display: none;
}

.redact-item .text-input ,
.input-block,
.input-block input,
.description-text textarea,
.redact-item input[type='file'] {
    border: none;
    border-bottom: 2px solid #fff ;
    outline: none;
    background-color: #fff;
    padding: 14px;
    border-radius: 4px;
    transition: .3s linear;
}

.redact-item .text-input:focus,
.input-block input:focus,
.description-text textarea:focus {
    border-bottom: 2px solid #FF8103 !important;
}

.redact-item .text-input:hover,
.input-block input:hover,
.redact-item input[type='file']:hover {
    background: #eee1;
    border-bottom: 2px solid #eee1;
}


.redact-item > p,
.description > p {
    font-weight: bold;
}

.redact-container .code {
    width: 50%;
}

.text-input {
    width: 87%;
}

.input-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;
    margin: 0;
    line-height: 0px;
}

.input-block p {
    color: #A4A6AA;
    padding: 0 15px;
}

.input-block input {
    width: 90%;
}


/* Decsription */

.description {
    width: 100%;
    margin: 8px;
}

.description-redact {
    background-color: #fff;
    display: flex;
    padding: 5px;
    border-bottom: 1px solid #E4E4E4;
}

.description-item {
    margin: 6px;
    filter: opacity(75%);
}

.description-item:hover {
    filter: opacity(100%);
}

.description-text  {
    background-color: #fff;
}

.description-text textarea{
    font-size: 14px;
    height: 17vh;
    width: 96%;
    resize: none;
    margin: 5px;
}