.header,
.nav-container,
.button-container {
    display: flex;
}

.header {
    width: 100%;
    justify-content: space-between;
}

.nav-container {
    width: 100%;
    align-items: center;
    padding: 0 20px;
}

.nav-item {
    margin: 10px 20px;
}

.nav-item * {
    font-family: Inter;
}

.back{
    display: flex;
    align-items: center;
}

.button-container {
    justify-content: flex-end;
}

.nav-container .but-1, .but-2 {
    font-size: 16px;
    border-radius: 4px;
    width: 200px;
    outline: none;
    padding: 15px;
    transition: .3s linear;
}

.but-1,
.but-2:hover {
    font-weight: normal;
    color: #FF8103;
    border: 1px solid #FF8103;
    background: #F4F6F6FF;
}

.but-2,
.but-1:hover {
    font-weight: 600;
    color: #fff;
    border: 1px solid #F4F6F6FF;
    background-color: #FF8103;
}