.preview {
    width: 25%;
    background-color: #fff;
    padding: 10px;
    border-radius: 5px;
}

.preview > p {
    padding: 0 20px;
    font-size: 16px;
    color: #A4A6AA;
}

.preview-info {
    padding: 3px;
    margin: 12px;
    background: #F4F6F6;
    border-radius: 12px;
}

.preview-info img{
    width: 100%;
}

.preview-info  p {
    margin: 6px;
}

.preview-info .rates,
.preview-info .products {
    color: #A4A6AA;
    font-size: 14px;
}

.preview-info .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.preview-info  .name-product  {
    font-weight: bold;
    font-size: 18px;
}

.preview-info .price {
    color: #FF8103;
    font-weight: bold;
    font-size: 22px;
    display: flex;
    align-items: center;
}

